// reset everything
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}


html {
  // reset the font size to 10px
  font-size: 62.5%;
}

html, body {
  font-family: "Helvetica Neue", Arial, Helvetica, sans-serif;
}

.content {
  padding-top: 5.2rem;
}

p {
  font-size: 1.6rem;
}

a {
  color: $color-astral;
  text-decoration: none;

  &:hover {
    text-decoration: underline; 
  }
}

hr {
  border-top: 0.1rem solid $color-gallery;
  margin: 1.5rem;
}

.text-justify{
  text-align: justify;
  p{
    padding: 1rem 0;
  }
}

.youtube-video-16-by-9 {
  // border: 1rem solid red;
  width: 100%;
  // get the percentage for 16 by 9
  padding-top: 52.26%;
  position: relative;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    // z-index: -1;
  }
}

.image img {
  max-width: 100%;
}

.list-padded {
  font-size: 1.4rem;
  padding: 1rem 3rem;
}

h3 {
  font-size: 1.4rem;
  padding: 1.5rem;
}

h4 {
  padding: 1.5rem;
  font-size: 1.3rem;
}

h3 + h4 {
  padding-top: 0;
}

.landing__content {
  ul {
    li {
      font-size: 1.4rem;
      text-align: left;
    }
    margin: 1rem 1rem 1rem 2rem;
  }
}

.image-inline {
  margin-bottom: -0.4rem;
  padding: 0 0.45rem;
}

.no-disc {
  list-style-type: none;
}

.full-width {
  max-width: 100%;
}

@import 'landing-content';